// Header Styles

.header {
  padding: 0;
  background-color: #fefefe;
  box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
}
.header > .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 72px;
}

.nav-btn-wrapper {
  display: flex;
  align-items: baseline;
  gap: 30px;
  @media screen and (min-width: 768px) and (max-width: 1280px) {
    gap: 10px;
  }
}

.menu__list {
  display: flex;
  gap: 28px;
  @media screen and (min-width: 768px) and (max-width: 1280px) {
    gap: 20px;
  }
}

.menu__link {
  text-decoration: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.1;
  color: #000000;
  &:hover,
  &:focus {
    text-decoration: underline;
    font-weight: 500;
    color: #bbf330;
  }
}

