.order-btn {
  padding: 6px 18px;
  background: #bbf330;
  border-radius: 30px;
  border: none;
  cursor: pointer;

  font-weight: 500;
  font-size: 13px;
  line-height: 150%;
  color: #2c2c2c;

  @media screen and (max-width: 767px) {
    display: none;
  }
  &:hover,
  &:focus {
    background-color: #b5ff00;
  }
}
.menu-icon {
  fill: #7eb101;
  &:hover,
  &:focus {
    fill: #ffe550;
  }
}
